import React, { useContext } from "react";
import { Switch, Route } from "react-router-dom";
import { StoreContext } from "../../store/StoreProvider";
import Order from "./subcomponents/Order";

const Checkout = () => {
  const { checkoutStep } = useContext(StoreContext);
  return (
    <div className="container">
      <div className="d-flex justify-content-around m-4">
        <div className={checkoutStep > 0 && "font-weight-bold"}>Logowanie</div>
        <div className={checkoutStep > 1 && "font-weight-bold"}>
          Dane do wysyłki i płatność
        </div>
        <div className={checkoutStep > 2 && "font-weight-bold"}>
          Podsumowanie
        </div>
        <div className={checkoutStep > 3 && "font-weight-bold"}>Gotowe</div>
      </div>
      <Switch>
        <Route path="/checkout/order" render={() => <Order />} />
      </Switch>
    </div>
  );
};

export default Checkout;
